<script>
import { ref, reactive,onMounted } from 'vue'
import Header from "../../components/global/header/HeaderArea.vue"
import Footer from "../../components/global/footer/FooterArea.vue"
import { useRouter } from "vue-router"
import { Toast } from '../../components/global/swal'
import { apiPostPassword, apiCaretakerPostPassword } from "@/api/api"
import { focusInput } from '@/utils/fixFooter'

export default {
  setup() {
    const router = useRouter()
    const inputData = reactive({
      "oldPassword": "",
      "newPassword": ""
    })
    const newPassword2 = ref("")
    const msg = ref("")

    const form = ref(null)
    const save = async (e) => {
      e.preventDefault()
      if (form.value.reportValidity()) {
        if (inputData.oldPassword == inputData.newPassword) {
          Toast('新密碼與舊密碼不可相同')
        } else if (inputData.newPassword != newPassword2.value) {
          Toast('新密碼不相符，請重新輸入')
        } else {
          //post API
          if (process.env.VUE_APP_MODE == 'purple') {
            msg.value = await apiPostPassword(inputData)
          } else {
            msg.value = await apiCaretakerPostPassword(inputData)
          }

          if (msg.value.message) {
            Toast("密碼錯誤，請重新輸入")
          }
          if (!msg.value.message) {
            Toast("密碼已修改")
            router.push({ path: "/member" })
          }
          inputData.oldPassword = ""
          inputData.newPassword = ""
          newPassword2.value = ""

        }
      }
    }

    onMounted(() => {
      focusInput()
    })

    let isShowPassword = ref(0)
    let isShowPassword2 = ref(0)
    let isShowPassword3 = ref(0)
    const showPassword = () => { isShowPassword.value = isShowPassword.value == 0 ? 1 : 0 }
    const showPassword2 = () => { isShowPassword2.value = isShowPassword2.value == 0 ? 1 : 0 }
    const showPassword3 = () => { isShowPassword3.value = isShowPassword3.value == 0 ? 1 : 0 }
    return { inputData, save, form, newPassword2, isShowPassword, isShowPassword2, isShowPassword3, showPassword, showPassword2, showPassword3 }
  },
  components: {
    Header, Footer
  },
}
</script>

<template>
  <Header title="變更密碼" backToPath="/member" />
  <div class="password">
    <form ref="form">
      <div class="mb-3">
        <label class="form-label">舊密碼<span class="star">*</span></label>
        <div class="passwordArea" v-if="isShowPassword == 0">
          <input type="password" class="form-control" placeholder="請輸入舊密碼" v-model="inputData.oldPassword" required>
          <a class="nav-link" @click="showPassword"><i class="far fa-eye"></i></a>
        </div>
        <div class="passwordArea" v-if="isShowPassword == 1">
          <input type="text" class="form-control" placeholder="請輸入舊密碼" v-model="inputData.oldPassword" required>
          <a class="nav-link" @click="showPassword"><i class="far fa-eye-slash"></i></a>
        </div>
      </div>
      <div class="mb-3">
        <label class="form-label">新密碼<span class="star">*</span></label>
        <div class="passwordArea" v-if="isShowPassword2 == 0">
          <input type="password" class="form-control" placeholder="請輸入新密碼" v-model="inputData.newPassword"
            pattern="^(?=.*[A-Za-z])(?=.*[0-9]).{6,}$" title="最少6個字元，需有英文及數字" required>
          <a class="nav-link" @click="showPassword2"><i class="far fa-eye"></i></a>
        </div>
        <div class="passwordArea" v-if="isShowPassword2 == 1">
          <input type="text" class="form-control" placeholder="請輸入新密碼" v-model="inputData.newPassword"
            pattern="^(?=.*[A-Za-z])(?=.*[0-9]).{6,}$" title="最少6個字元，需有英文及數字" required>
          <a class="nav-link" @click="showPassword2"><i class="far fa-eye-slash"></i></a>
        </div>
      </div>
      <div class="mb-3">
        <label class="form-label">確認密碼<span class="star">*</span></label>
        <div class="passwordArea" v-if="isShowPassword3 == 0">
          <input type="password" class="form-control" placeholder="請再次輸入新密碼" v-model="newPassword2" required>
          <a class="nav-link" @click="showPassword3"><i class="far fa-eye"></i></a>
        </div>
        <div class="passwordArea" v-if="isShowPassword3 == 1">
          <input type="text" class="form-control" placeholder="請再次輸入新密碼" v-model="newPassword2" required>
          <a class="nav-link" @click="showPassword3"><i class="far fa-eye-slash"></i></a>
        </div>
      </div>
      <div class="mt-4b btn-container">
        <div class="row">
          <div class="col">
            <button class="btn btn-outline-primary" type="button" @click="save">儲存資料</button>
          </div>
          <div class="col">
            <button class="btn btn-outline-primary" type="button"
              @click="$router.push({ path: '/member' })">取消返回</button>
          </div>
        </div>
      </div>
    </form>
  </div>
  <Footer />
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.password {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
  padding: 1.25rem;

}

.passwordArea {
  position: relative;

  a {
    position: absolute;
    top: 0.5rem;
    right: 1rem;
  }
}
</style>